html,
body,
h1 {
  margin: 0;
  padding: 0; }

section {
  max-width: 960px;
  margin: 0 auto;
  padding-top: 60px; }
  section h1 {
    font-size: 3rem; }
  section p {
    margin: 40px 0;
    font-size: 1.2rem;
    line-height: 1.8; }

img {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto; }

.header {
  position: fixed;
  left: 0;
  top: 0;
  background-color: dodgerblue;
  height: 60px;
  width: 100%;
  font-size: 22px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2); }
  .header span {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-transform: capitalize;
    height: 100%; }

.header {
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s; }

.hide {
  -webkit-transform: translateY(-60px);
          transform: translateY(-60px); }

.App {
  font-family: sans-serif;
  text-align: center;
}

